import { chain } from 'wagmi';

import { isProduction } from './analytics/Mixpanel';
import base from './config/base';
import baseGoerli from './config/baseGoerli';
import ethereum from './config/ethereum';
import goerli from './config/goerli';
import linea from './config/linea';
import { Configuration } from './protocol/config';
import { baseChain, baseGoerliChain, lineaChain } from './utils/CustomChains';
import { marketsData } from "./ui-config/marketConfig";

let configurations: { [env: string]: Configuration } = {
  ...ethereum,
  ...baseGoerli,
  // ...base,
  // ...linea,
  // ...goerli,
};

export let ConfigChains = [chain.mainnet, baseGoerliChain];

/*if (isProduction) {
  configurations = {
    ...ethereum,
    ...base,
    ...linea,
  };
  ConfigChains = [chain.mainnet, baseChain, lineaChain];
}*/

export default configurations;

export const getSupportedChains = (): number[] =>
  Object.keys(configurations).map((i) => Number(i));

Object.entries(marketsData).map(([key, data])=> {
  if (!getSupportedChains().includes(data.chainId)) console.error(`* ChainId: ${data.chainId} is not present in the src/config.ts please add that into it`);
})
