import { BigNumber } from 'ethers';
import { useCallback, useEffect, useState } from 'react';

import { useBlockNumber } from '../../state/application/hooks';
import { useGetAccount, useGetChainId } from "../../utils/NetworksCustomHooks";
import useCore from '../useCore';

type State = {
  isLoading: boolean;
  value: BigNumber;
};

const useGetNativeTokenBalance = () => {
  const [balance, setBalance] = useState<State>({ isLoading: true, value: BigNumber.from(0) });

  const core = useCore();
  const account = useGetAccount();
  const blockNumber = useBlockNumber();
  const chainId = useGetChainId();

  const fetchBalance = useCallback(async () => {
    if (!account) {
      setBalance({ isLoading: false, value: BigNumber.from(0) });
      return;
    }

    const bal = await core._provider[chainId].getBalance(account);
    setBalance({ isLoading: false, value: bal });
  }, [account, chainId]);

  useEffect(() => {
    if (core.isUnlocked) {
      fetchBalance().catch((err) =>
        console.error(`Failed to fetch token balance of ${account}: ${err.stack} `),
      );
    }
  }, [blockNumber, account, core.isUnlocked, fetchBalance]);

  return balance;
};

export default useGetNativeTokenBalance;
