import { BigNumber } from 'ethers';
import { useCallback, useEffect, useState } from 'react';

import { useBlockNumber } from '../../../state/application/hooks';
import { useGetAccount, useGetChainId } from '../../../utils/NetworksCustomHooks';
import useCore from '../../useCore';

type State = {
  isLoading: boolean;
  value: BigNumber;
};

export default (contractPrefix: string) => {
  const [value, setValue] = useState<State>({ isLoading: true, value: BigNumber.from(0) });

  const core = useCore();
  const account = useGetAccount();
  const chainId = useGetChainId();
  const blockNumber = useBlockNumber();

  const fetchValue = useCallback(async () => {
    if (!account || true) {
      // disable for now
      setValue({ isLoading: false, value: BigNumber.from(0) });
      return;
    }

    const contract = core.getGovernance(contractPrefix, chainId);
    const value = await contract.getStabilityFee();

    setValue({ isLoading: false, value: value });
  }, [account, core, contractPrefix, chainId]);

  useEffect(() => {
    if (core.isUnlocked) {
      fetchValue().catch((err) =>
        console.error(`Failed to fetch stability token and fee: ${err}`),
      );
    }
  }, [blockNumber, core.isUnlocked, account, fetchValue]);

  return value;
};
