import React, { useMemo, useState } from 'react';
import { useParams } from 'react-router-dom';

import Grid from '@material-ui/core/Grid';

import Container from '../components/Container';
import config from '../config';
import useCore from '../hooks/useCore';
import RedeemTabContent from '../modules/redeem/Redeem';
import RedeemDesc from '../modules/redeem/RedeemDesc';
import PageHeader from '../theme/components/PageHeader';
import { useGetAccount, useGetChainId } from '../utils/NetworksCustomHooks';
import LoanInfo from '../views/LoanInfo/LoanInfo';
import PageLayout from "../layouts/PageLayout";


const Redeem = () => {
  // @ts-ignore
  const { id } = useParams();
  const [trove, setTrove] = useState<string>('');
  const chainId = useGetChainId();
  const account = useGetAccount();

  const core = useCore();
  const isTroveValid: boolean = useMemo(() => {
    const troves = core.getAllTroves(chainId, account);
    if (troves.includes(id)) {
      setTrove(id);
      return true;
    } else {
      return false;
    }
  }, [core, chainId, account, id]);

  if (!isTroveValid) {
    window.location.href = `/#/redeem/${config[chainId].defaultCollateral}`;
    return <div />;
  }

  return (
    <PageLayout
      title={`Redeem ARTH for ${trove}`}
      subTitle={`Reedem ARTH for its underlying collateral.`}
    >
      <Grid container className="m-t-24" spacing={2}>
        <Grid item lg={6} md={6} sm={12} xs={12}>
          <RedeemTabContent trove={trove} />
        </Grid>
        <Grid item lg={6} md={6} sm={12} xs={12}>
          <RedeemDesc />
          <LoanInfo trove={trove} />
        </Grid>
      </Grid>
    </PageLayout>

  );
};

export default Redeem;
