import { BigNumber } from "ethers";
import { useCallback } from "react";



import { useAddPopup } from "../../state/application/hooks";
import { useTransactionAdder } from "../../state/transactions/hooks";
import { useGetChainId } from "../../utils/NetworksCustomHooks";
import { getDisplayBalance } from "../../utils/formatBalance";
import formatErrorMessage from "../../utils/formatErrorMessage";
import useCore from "../useCore";


// eslint-disable-next-line import/no-anonymous-default-export
export default function (symbol: string, amount: BigNumber) {
  const core = useCore();
  const addPopup = useAddPopup();
  const addTransaction = useTransactionAdder();
  const chainId = useGetChainId();

  const action = useCallback(
    async (callback?: () => void): Promise<void> => {
      const weth = core.contracts('WETH_WRAPPER', chainId);

      try {
        const response = await weth.deposit({ value: amount });

        addTransaction(response, {
          summary: `Wrap ${Number(
            getDisplayBalance(amount, 18, 3),
          ).toLocaleString()} ${symbol}`,
        });

        if (callback) callback();
      } catch (e) {
        addPopup({
          error: {
            message: formatErrorMessage(e?.data?.message || e?.message),
            stack: e?.stack,
          },
        });
      }
    },
    [core, chainId, amount, addTransaction, symbol, addPopup],
  );

  return action;
}
