import { BigNumber } from 'ethers';
import { useCallback, useEffect, useState } from 'react';

import { useBlockNumber } from '../../state/application/hooks';
import { useGetAccount, useGetChainId } from '../../utils/NetworksCustomHooks';
import { LOADING_DEFAULT_BASIC_STATE } from '../../utils/constants';
import { BasicState } from '../../utils/interface';
import useCore from '../useCore';

export default (contractPrefix: string) => {
  const [value, setValue] = useState<BasicState>(LOADING_DEFAULT_BASIC_STATE);

  const core = useCore();
  const account = useGetAccount();
  const activeChainId = useGetChainId();
  const blockNumber = useBlockNumber();

  const contract = core.getStabilityPool(contractPrefix, activeChainId);

  const fetchValue = useCallback(async () => {
    if (!account) {
      setValue({ isLoading: false, value: BigNumber.from(0) });
      return;
    }

    const response = await contract.getTotalARTHDeposits();

    setValue({
      isLoading: false,
      value: response,
    });
  }, [account, contract]);

  useEffect(() => {
    if (core.isUnlocked) {
      fetchValue().catch((err) => console.error(`Failed to fetch tvl in (arth) %: ${err}`));
    }
  }, [core.isUnlocked, account, fetchValue, blockNumber]);

  return value;
};
