import styled from 'styled-components';
import { useSwitchNetwork } from 'wagmi';

import theme from '../../theme';
import Button from '../../theme/components/Button';
import TextWrapper from '../../theme/components/TextWrapper';
import { MarketDataType } from '../../ui-config/marketConfig';
import useGetProtocolCollateral from "../../hooks/state/useGetProtocolCollateral";
import DataField from "../../theme/components/DataField";
import Numeral from "numeral";
import { getDisplayBalance } from "../../utils/formatBalance";
import { useEffect } from "react";

const ChainsCard = (props: { data: MarketDataType }) => {
  const { switchNetwork, status, isSuccess } = useSwitchNetwork();
  const entireSystemCollateral = useGetProtocolCollateral(props.data.chainId);

  useEffect(() => {
    if (isSuccess) window.open("/#/loans/all-loans", "_self")
  })

  return (
    <Card className={'material-primary'}>
      <CardUpper style={{ background: props.data.themeColor }}>
        <CardImg src={props.data.iconUrl} alt={props.data.marketTitle} />
      </CardUpper>
      <CardContent>
        <TextWrapper
          text={props.data.marketTitle}
          align={'center'}
          fontSize={24}
          fontWeight={600}
        />
        <TextWrapper
          text={props.data.desc}
          align={'center'}
          className={'m-t-16'}
          Fcolor={theme.color.transparent[100]}
        />
        {/* <div className={"m-t-24"}>
          <DataField
            label={"TVL"}
            labelFontWeight={600}
            isValueLoading={entireSystemCollateral.isLoading}
            valueFontWeight={600}
            value={`${Numeral(getDisplayBalance(entireSystemCollateral.value, 18, 3)).format('0,0')} ETH`}
          />
        </div>*/}
        <div className={'single-line-center-center m-t-32'}>
          <div className={'w-50'}>
            <Button
              text={'Open Protocol'}
              variant={'transparent'}
              size={'sm'}
              onClick={() => switchNetwork?.(props.data.chainId)}
            />
          </div>
        </div>
      </CardContent>
    </Card>
  );
};

export default ChainsCard;

const Card = styled.div`
  padding: 0;
  border: 6px;
`;

const CardUpper = styled.div`
  height: 120px;
  border-top-left-radius: 6px;
  border-top-right-radius: 6px;
  display: flex;
  justify-content: center;
  align-items: center;
`;

const CardImg = styled.img`
  width: 80px;
  height: 80px;
`;

const CardContent = styled.div`
  padding: 24px;
`;
