import React, { createContext, useEffect, useState } from 'react';
import { useDispatch } from 'react-redux';

import config from '../config';
import BasisCash from '../protocol';
import { useGetAccount, useGetChainId } from '../utils/NetworksCustomHooks';

export interface BasisCashContext {
  core: BasisCash;
}

export const Context = createContext<BasisCashContext>({ core: null });

interface IProps {
  children: any;
}

export const ProtocolProvider = (props: IProps) => {
  const { children } = props;
  const chainId = useGetChainId();
  const account = useGetAccount();
  const [core, setCore] = useState<BasisCash>();
  const dispatch = useDispatch();

  useEffect(() => {
    if (!core && config) {
      const newCore = new BasisCash(config, chainId);
      if (account) {
        newCore.unlockWallet(window.ethereum, account, dispatch, chainId);
      }
      setCore(newCore);
    } else if (account) {
      core.unlockWallet(window.ethereum, account, dispatch, chainId);
    }
  }, [account, core, dispatch, window.ethereum, chainId]);

  return <Context.Provider value={{ core }}>{children}</Context.Provider>;
};
