import React, { useMemo, useState } from 'react';
import { useParams } from 'react-router-dom';

import Grid from '@material-ui/core/Grid';

import config from '../../config';
import useCore from '../../hooks/useCore';
import InfoTip from '../../theme/components/InfoTip';
import PageHeader from '../../theme/components/PageHeader';
import { useGetAccount, useGetChainId } from '../../utils/NetworksCustomHooks';
import LoanInfo from '../LoanInfo/LoanInfo';

import AddLoanSection from './components/AddLoanSection';
import UnclaimedCollateral from './components/UnclaimedCollateral';


const AddLoan = () => {
  const { id } = useParams<{ id: string }>();
  const [trove, setTrove] = useState<string>('');
  const chainId = useGetChainId();
  const account = useGetAccount();

  const core = useCore();
  const isTroveValid: boolean = useMemo(() => {
    const troves = core.getAllTroves(chainId, account);
    if (troves.includes(id)) {
      setTrove(id);
      return true;
    } else {
      return false;
    }
  }, [core, chainId, account, id]);

  if (!isTroveValid) {
    window.location.href = `/#/loan/add/${config[chainId].defaultCollateral}`;
    return <div />;
  }

  if (isTroveValid && trove !== '') {
    return (
      <div className="custom-container">
        <PageHeader
          title={`Mint ARTH`}
          subTitle={`Mint ARTH by putting down your ETH as collateral.`}
        />
        <div className={'m-b-24'}>
          <UnclaimedCollateral contractPrefix={id} />
        </div>

        <Grid container spacing={2}>
          <Grid item lg={6} md={6} sm={12} xs={12}>
            <div className={'m-b-24'}>
              <InfoTip
                type={'Info'}
                msg={
                  <div>
                    <b>Mint ARTH and get special access to our Discord </b> <br />
                    Users who mint arth will get special roles in our discord that give you
                    access to exclusive channels. Visit
                    <a
                      target="_blank"
                      rel="noopener noreferrer"
                      href="https://guild.xyz/mahadao"
                      className={'links bold'}
                    >
                      &nbsp; Guild.xyz
                    </a>{' '}
                    to learn more.
                  </div>
                }
              />
            </div>
            <div className={'custom-mahadao-container'}>
              <div className={'custom-mahadao-container-content'}>
                <AddLoanSection trove={trove} />
              </div>
            </div>
          </Grid>
          <Grid item lg={6} md={6} sm={12} xs={12}>
            {/*<LoanDesc />*/}
            <LoanInfo trove={trove} />
          </Grid>
        </Grid>
      </div>
    );
  }
};

export default AddLoan;
