import { BigNumber } from 'ethers';
import { useCallback, useEffect, useState } from 'react';

import { useBlockNumber } from '../../../state/application/hooks';
import { useGetAccount, useGetChainId } from '../../../utils/NetworksCustomHooks';
import useCore from '../../useCore';

type State = {
  isLoading: boolean;
  value: BigNumber;
};

export default (contractPrefix: string) => {
  const [value, setValue] = useState<State>({ isLoading: true, value: BigNumber.from(0) });

  const core = useCore();
  const account = useGetAccount();
  const blockNumber = useBlockNumber();
  const chainId = useGetChainId();

  const fetchValue = useCallback(async () => {
    if (!account) {
      setValue({ isLoading: false, value: BigNumber.from(0) });
      return;
    }

    const contract = core.getStabilityPool(contractPrefix, chainId);
    const value = await contract.getTotalARTHDeposits();
    if (value.lte(0)) {
      setValue({ isLoading: false, value: BigNumber.from(0) });
      return;
    }

    const currentDeposit = await contract.getCompoundedARTHDeposit(core.myAccount);
    setValue({ isLoading: false, value: currentDeposit.mul(100).div(value) });
  }, [account, core, contractPrefix, chainId]);

  useEffect(() => {
    if (core.isUnlocked) {
      fetchValue().catch((err) =>
        console.error(`Failed to fetch useGetPoolShare() for ${contractPrefix}: ${err}`),
      );
    }
  }, [core.isUnlocked, core.myAccount, account, blockNumber, fetchValue, contractPrefix]);

  return value;
};
