import React from 'react';
import { NavLink } from 'react-router-dom';
import styled from 'styled-components';

import config from '../../config';
import useOneTroveIsAlreadyOpen from '../../hooks/state/sortedTroves/useGetTroveIsAlreadyOpen';
import IconLoader from '../../theme/components/IconLoader';
import { useGetAccount, useGetChainId } from '../../utils/NetworksCustomHooks';

const Sidebar = () => {
  const account = useGetAccount();
  const chainId = useGetChainId();

  const troveIsAlreadyPresent = useOneTroveIsAlreadyOpen(config[chainId].defaultCollateral);

  return (
    <SidebarDiv>
      <SidebarContainer id={'sidebar_container'}>
        <TopSideBar>
          <StyledLink activeClassName="activeSidebar" to="">
            Home
          </StyledLink>
          <StyledLink activeClassName="activeSidebar" to="/loans/all-loans">
            Overview
          </StyledLink>
          {troveIsAlreadyPresent.value ? (
            <StyledLink
              activeClassName="activeSidebar"
              to={`/loan/details/${account ? account : ""}/${
                config[chainId].defaultCollateral
              }`}
            >
              View Loan
            </StyledLink>
          ) : (
            <StyledLink
              activeClassName="activeSidebar"
              to={"/loan/add/" + config[chainId].defaultCollateral}
            >
              Mint ARTH
            </StyledLink>
          )}
          <StyledLink
            exact
            activeClassName="activeSidebar"
            to={"/stability-pool/" + config[chainId].defaultCollateral}
          >
            Stability Pool
          </StyledLink>
          <StyledLink
            exact
            activeClassName="activeSidebar"
            to={"/redeem/" + config[chainId].defaultCollateral}
          >
            Redeem
          </StyledLink>
          <StyledLink exact activeClassName="activeSidebar" to="/price">
            Protocol Price
          </StyledLink>
          <StyledLinkHref href={"https://farming.mahadao.com/"} target={"_blank"}>
            <div className={"single-line-center-start"}>
              <p>Farming</p>
              <IconLoader iconName={"ArrowLinkColored"} iconType={"arrow"} />
            </div>
          </StyledLinkHref>
          <StyledLinkHref href={config[chainId].tradingLink} target={"_blank"}>
            <div className={"single-line-center-start"}>
              <p>Trade</p>
              <IconLoader iconName={"ArrowLinkColored"} iconType={"arrow"} />
            </div>
          </StyledLinkHref>
          {(chainId === 5 || chainId === 80001) && (
            <StyledLink exact activeClassName="activeSidebar" to="/faucet">
              Faucet
            </StyledLink>
          )}
        </TopSideBar>
        <BottomSideBar>
          <StyledLinkHref href={config[chainId].analyticsLink} target={'_blank'}>
            <div className={'single-line-center-start'}>
              <p>Analytics</p>
              <IconLoader iconName={'ArrowLinkColored'} iconType={'arrow'} />
            </div>
          </StyledLinkHref>
          <StyledLinkHref href="https://docs.arth.loans/" target={'_blank'}>
            <div className={'single-line-center-start'}>
              <p>Documentation</p>
              <IconLoader iconName={'ArrowLinkColored'} iconType={'arrow'} />
            </div>
          </StyledLinkHref>
        </BottomSideBar>
      </SidebarContainer>
    </SidebarDiv>
  );
};

export default Sidebar;

const SidebarDiv = styled.div`
  min-width: 200px;
  max-width: 200px;
  min-height: 100vh;
  max-height: 100vh;
  @media (max-width: 800px) {
    display: none;
  }
`;

const SidebarContainer = styled.div`
  width: 200px;
  background: #1f1e1e;
  border-radius: 6px;
  border: 1px solid;
  border-image-source: linear-gradient(
    180deg,
    rgba(255, 255, 255, 0.1) 0%,
    rgba(255, 255, 255, 0) 100%
  );
  position: fixed;
  top: 0;
  padding: 16px;
  margin-top: calc(112px);
  min-height: calc(100vh - 144px);

  @media (max-width: 600px) {
    margin-top: calc(112px);
    min-height: calc(100vh - 144px);
  }
`;

const StyledLink = styled(NavLink)`
  padding: 8px 16px;
  width: 100%;
  border-radius: 2px;
  display: block;
  font-style: normal;
  font-weight: 600;
  font-size: 14px;
  line-height: 20px;
  color: #ffffff88;
  margin-bottom: 8px;
  &:hover {
    outline: 1px solid rgba(255, 255, 255, 0.08);
    color: #ffffff88;
    opacity: 1;
  }
  &:active {
    background: rgba(255, 255, 255, 0.01);
    border-radius: 2px;
    width: 100%;
    color: #ffffff88;
    opacity: 1;
  }
`;

const StyledLinkHref = styled.a`
  padding: 8px 16px;
  width: 100%;
  border-radius: 2px;
  display: block;
  font-style: normal;
  font-weight: 600;
  font-size: 14px;
  line-height: 20px;
  color: #ffffff88;
  margin-bottom: 8px;
  &:hover {
    outline: 1px solid rgba(255, 255, 255, 0.08);
    color: #ffffff88;
    opacity: 1;
  }
  &:active {
    background: rgba(255, 255, 255, 0.01);
    border-radius: 2px;
    width: 100%;
    color: #ffffff88;
    opacity: 1;
  }
`;

const TopSideBar = styled.div`
  padding-bottom: 177px;
`;

const BottomSideBar = styled.div`
  position: absolute;
  bottom: 0;
  padding: 16px 0;
  width: calc(100% - 32px);
  border-top: 1px solid rgba(255, 255, 255, 0.08);
`;
