import { BigNumber } from 'ethers';
import { useCallback, useEffect, useState } from 'react';
import { useSelector } from 'react-redux';

import { AppState } from '../../state';
import { useGetAccount, useGetChainId } from '../../utils/NetworksCustomHooks';
import useCore from '../useCore';
import { value } from "numeral";

type State = {
  isLoading: boolean;
  value: BigNumber;
};

export default (chainId?: number) => {
  const [value, setValue] = useState<State>({ isLoading: true, value: BigNumber.from(0) });

  const core = useCore();
  const activeChainId = chainId || useGetChainId();
  const account = useGetAccount();

  const fetchValue = useCallback(async () => {
    const contract = core.getTroveManager('ETH', activeChainId);
    const value = await contract.getEntireSystemColl();

    setValue({
      isLoading: false,
      value: value,
    });
  }, [core, activeChainId]);

  useEffect(() => {
    if (core.isUnlocked) {
      fetchValue().catch((err) =>
        console.error(`Failed to fetch entire protocol collateral %: ${err}`),
      );
    }
  }, [core.isUnlocked, account, fetchValue, activeChainId]);

  return value;
};
